import { Card, Grid } from "semantic-ui-react";

import PrecioSpan from "./PrecioSpan";

const Precio = props => {
  const { percent_change_1h, price } = props.datos;
  console.log(props);
  return (
    <Card color="green">
      <Card.Content>
        <Card.Header textAlign="left">Precio del bitcoin</Card.Header>
      </Card.Content>
      <Card.Content extra>
        <Card.Description textAlign="left">
          <h3>Precio Actual: $ {price.toFixed(2)}</h3>
          <Grid columns="equal">
            <Grid.Column>
              Última hora: {percent_change_1h.toFixed(2)} %
            </Grid.Column>
            <Grid.Column>
              Últimas 24 horas: {percent_change_1h.toFixed(2)} %
            </Grid.Column>
            <Grid.Column>
              Última hora: {percent_change_1h.toFixed(2)} %
            </Grid.Column>
          </Grid>
        </Card.Description>
      </Card.Content>
    </Card>
  );
};

export default Precio;
